import { useEffect, useState } from 'react';
import {departmentData, positionData} from '../data/dummydata.js';
import { getCurrentDateTime } from '../utils/currentdatetime.js';

export default function RequestWorkOriderGUI(){
    //const [departments,setDepartmets] = useState(departmentData);
    const [inputs, setInputs] = useState({department:1,position:1});
    const departments = departmentData;
    const positions = positionData;
    //const currentDateTime = new Date;
    
    //const [requestdatetime,setRequestdatetime] = useState(getCurrentDateTime());
    //const [descripiton, setDescription] = useState('')
    const [submitted, setSubmitted] = useState(false);

    let currentDateTime;
    
    useEffect(()=>{
      currentDateTime = getCurrentDateTime();
      //setRequestdetime(currentDateTime);
      console.log(currentDateTime);
      setInputs({...inputs,requestdatetime: currentDateTime});      
    },[currentDateTime]);

    function submitWorkorder(e){
        //const requestdatetime = formData.get('txtrequestdatetime');
        e.preventDefault();
        setSubmitted(true);
        //console.log('hello : ' + inputs.descripiton);
    }

    function handleRequestDateTimeChanged(e){
      //setRequestdatetime(e.target.value);
      setInputs({...inputs,requestdatetime: e.target.value})
    }

    return(<div className='container'>
            <form onSubmit={submitWorkorder}>
              <p style={{'margin-top':'20px','margin-bottom':'7px'}}>Request Date/time</p>
              <input type='datetime-local' id='idtxtrequestdatetime' name='txtrequestdatetime' value={inputs.requestdatetime}
                onChange={handleRequestDateTimeChanged}
              />
              <p>Department Name</p>
              <SelectedBox data={departments} displayData={'departmentfullname'} dataValue={'departmentid'} 
                onSelectedValue={(selectedValue)=>setInputs({...inputs,department:selectedValue})}/>
              <p>Position</p>
              <SelectedBox data={positions} displayData={'positionfullname'} dataValue={'positionid'} 
                onSelectedValue = {(selectedValue) => setInputs({...inputs,position:selectedValue})} />
              <p>Work Order Description</p>
              <textarea name='description' rows={4} cols={40} value={inputs.description} onChange={(e)=> {
                //if(submitted) setSubmitted(false);
                setInputs({...inputs,description: e.target.value});
              }} /><br/>
              <button type="submit" name="button" value="submit">SAVE</button>
            </form>   
            {submitted ? (<>
              <p>{inputs.requestdatetime}</p>
              <p>{inputs.department}</p>
              <p>{inputs.position}</p>
              <p>{inputs.description}</p>              
            </>) : ''}     
          </div>);
} 

