function SelectedBox({data, dataValue, displayData,onSelectedValue}){
    /*data.map((d,index) => {
        return(<div>
            <p>Hello{d.departmentname}</p>
        </div>)
    })*/
    /*data.map((d,i)=> {return(<p>{d.departmentname}</p>)})*/
    return(
        <div>
            <select onChange={(e) => onSelectedValue(e.target.value)}>
                {data.map((d,i) =>{                   
                  return (<option value={d[dataValue]} selected={i===0 ? true : false}>{d[displayData]}</option>)
                })}
            </select>
        </div>
    );
}
