import { useState } from "react";
import { usePosition, usePositionDispatch } from "./positioncontext"

export default function AddPosition(){
    const disptach = usePositionDispatch();
    const [position,setPosition] = useState({});
    return(
        <div className="divcontainer">
            <input name='positionname' placeholder="Positin Name" value={position.positionname} onChange={(e) => setPosition({...position, positionname : e.target.value})}/>
            <input name='positionfullname' placeholder="Postion Full Name" value={position.positionfullname} onChange={(e)=>setPosition({...position,positionfullname: e.target.value})}/>
            <button onClick={()=>{            
                //setPosition({...position,positionid:20});
                //console.log(position.positionid + "  " + position.positionname + "  " +position.positionfullname);
                disptach({
                    type: 'added',
                    position: position,
                });
            }}>Add</button>
        </div>)
}
