import { db } from "../dbmodule/firebase";
import { addDoc, getDocs, collection } from "firebase/firestore";

export default function departmentReducer(departments, action){
    switch (action.type){
        case 'added':{
            //console.log("department reducer " + action.department.departmentid + " " + action.department.departmentname + "  " + action.department.departmentfullname);
            //const department = {departmentid : action.departmentid++, departmentname: action.department.departmentname, departmentfullname: action.department.departmentfullname};            
            //addDepartment(department, departments);
            //const docRef = addDoc(collection(db,'sai_department'),{department: action.department})
            //console.log("department written to : " + docRef.id);
            return ([
                ...departments,{
                departmentid : action.department.departmentid, departmentname: action.department.departmentname, departmentfullname: action.department.departmentfullname
            }])
        }
        case 'updated':{
            return departments.map((d) => {
                if(d.departmentid === action.department.departmentid){
                    return action.department;
                }
                else{
                    return d;
                }
            })
        }
        case 'deleted':{
            return departments.filter((d) => d.departmentid !== action.departmentid)
        }
        case 'selected':{
            return action.departments;
        }
        default:{
            throw Error('Unknow action!');
        }
    }
}

export async function addDepartment(department)  {
    //e.preventDefault();
    try{
        //if(departments.filter((d)=> d.departmentname === department.departmentname) === 0){
            const docRef = await addDoc(collection(db,'sai_department'),{department: department})            
            console.log("department written to : " + docRef.id);
            return docRef;
       // }
        //else(console.log('duplicate data'));
    }catch(e){
        console.log("error : " + e);
    }
}
