import { useEffect, useReducer } from "react";
import departmentReducer,{addDepartment} from "./departmentreducer";
import {departmentData} from './../data/dummydata.js';
import AddDepartment from "./adddepartment.js";
import DepartmentList from "./departmentlist.js";

import { collection, getDocs } from "firebase/firestore";
import { db } from "../dbmodule/firebase.js";

export default function DepartmentMain(){
    //const [departments, disptach] = useReducer(departmentReducer, departmentData);
    const [departments, disptach] = useReducer(departmentReducer, []);
    let departmentid = departments.length;
    // alert (departmentid);
    function handleAddDepartment(department){
        //console.log('departmentid :' + departmentid);
        //console.log('hellle');
        //console.log("department main " + department.departmentid + " " + department.departmentname + "  " + department.departmentfullname);
        const d = {
            departmentid : departmentid++, 
            departmentname: department.departmentname, 
            departmentfullname: department.departmentfullname};    
        const docID = addDepartment(d, departments);
        console.log('doc ID :' + docID);
        disptach({
            type : 'added',  
            department : d,
        });
    }
    function handleUpdateDepartment(department){
        disptach({
            type: 'updated',
            department : department,
        });
    }
    function handleDeleteDepartment(departmentid){
        disptach({
            type: 'deleted',
            departmentid: departmentid,
        })
    }
    /*function handleSelectDepartment(){
        disptach({
            type: 'selected',
            departments : getDepartment(),
        })
    }*/

    const fetchDepartment = async() => {
        await getDocs(collection(db,'sai_department')).then(
            ((q) =>{
                const newData = q.docs.map((d) => ({
                    ...d.data(), id:d.id
                }));
            console.log(newData);
            //return newData;
            disptach({
                type: 'selected',
                departments: newData,
            });
        }))
    }

    useEffect(()=>{
        //departments = getDepartment();
        //handleSelectDepartment();
        //console.log(getDepartment());
        fetchDepartment();
        console.log('departments : ' + departments);
    },[]);
    return (
        <>        
        <AddDepartment onAddDepartment={handleAddDepartment} />        
        {/*<DepartmentList departments ={departments} onChangeDepartment={handleUpdateDepartment} onDeleteDepartment={handleDeleteDepartment}/>*/}
        </>
    )
}