import { SOPDocument } from "../data/dummydata";

export default function ITForms(){
    const sopdocumemt = SOPDocument;
    return(<>
        <div>
            {
                sopdocumemt.map((d) =>{ 
                    //console.log(d.documentpath);
                    return (<DocumentType document={d} />);
                })
                    //d.map((d1)=>(<><p>{d1.formref}</p></>))                
            }                
        </div>
    </>);
}

function DocumentType({document}){
    return(<>
        <h4 key={document.documenttype}>        
            {document.documenttype}
            <ul>
                {document.subdocument.map((sd)=>{
                    //console.log('document ::::' + document.documentpath);
                    return (<SubDocument subdocument={sd} documentpath={document.documentpath}/>)})}
            </ul>
        </h4>
    </>);
}

const getUrl = (subdocument,documentpath) => {
    const url = documentpath + '/' + subdocument;
    return (require(`${url}`));
}

function SubDocument({subdocument, documentpath}){
    console.log('"'+ documentpath + "/" + subdocument.filename+'"');
    return(<>
        <li key={subdocument.formref}>              
            {/*<a href={getUrl(subdocument.filename, documentpath)}
                download={subdocument.downloadfilename} 
                target="_blank"  rel="noreferrer">
                {subdocument.displaytext}
            </a>*/}
            <a href={`${documentpath}/${subdocument.filename}`}
                download={subdocument.downloadfilename} 
                target="_blank"  rel="noreferrer">
                {subdocument.displaytext}
            </a>
                      {/*<a href={require("./../data/sopdata/pr/saiprit001.pdf")} download='hello.pdf'>download</a>*/}
        </li>        
    </>);
}