import { createContext,useContext,useReducer } from 'react';
import {addDoc, collection} from 'firebase/firestore';

import {positionData} from './../data/dummydata';
import {db} from '../dbmodule/firebase';

const PositionContext = createContext(null);
const PositionDispatchContext = createContext(null);

export default function PositionProvide({children}){
    const [positions, dispatch] = useReducer(positionReducer, positionData);

    return(
        <PositionContext.Provider value={positions}>
            <PositionDispatchContext.Provider value={dispatch}>
                {children}
            </PositionDispatchContext.Provider>
        </PositionContext.Provider>        
    );
}

export function usePosition(){return useContext(PositionContext)}
export function usePositionDispatch(){return useContext(PositionDispatchContext)}

const addPosition = async (position) => {
    //e.preventDefault();
    try{
        const docRef = await addDoc(collection(db,'sai_position'),{position: position});
        console.log("Document written with ID: ", docRef.id);
    } catch (e) {
      console.error("Error adding document: " + e);
    }
}

function positionReducer(positions, action){
    const positionlength = positions.length;
    switch(action.type){
        case 'added':{
            const position = {positionid:positionlength+1,positionname:action.position.positionname,positionfullname:action.position.positionfullname};
            addPosition(position);
            //console.log(positionlength + "  " + action.position.positionname + "  " + action.position.positionfullname);
            return ([...positions,{positionid:positionlength+1,positionname:action.position.positionname,positionfullname:action.position.positionfullname
        }])}
        case 'changed':{
            return(positions.map((p)=>{
                if(p.positionid === action.position.positionid){
                    return action.position;
                }
                else return p;
            }))
        }
        case 'deleted':{
            return (positions.filter((p) => p.positionid !== action.positionid))
        }
        default:{
            console.log('unknow action');
        }
    }
}