import { useState } from "react";

export default function AddDepartment({onAddDepartment}){
//    const [departmentid, setDepartmentID] = useState(0);
//    const [departmentname,setDepartmentName] = useState('');
//    const [departmentfullname,setDepartmentFullname] = useState('');

    const [department, setDepartment] = useState({departmentid:1});

    return(        
        <div className="divcontainer" style={{border:'1px solid', width:'90%',textAlign:"center", 'grid-templete-columns': '30% 30% 30%'}}>
            {/*<input type='text' key='departmentid' name='departmentid' placeholder="department id" value={department.departmentid} onChange={(e) => handleChange(e)}/>*/}
            <div>
                <input type='text' key='departmentname' name='departmentname' placeholder="department name" value={department.departmentname} onChange={(e) => handleChange(e)}/>
            </div>
            <div>
                <input type='text' key='departmentfullname' name ='departmentfullname' placeholder="department full name" value={department.departmentfullname} onChange={(e) => handleChange(e)}/>
            </div>
            <div>
            <button onClick={() => {
                //console.log(department.departmentid + " " + department.departmentname + "  " + department.departmentfullname);
                onAddDepartment(department)}}>Save</button>
            </div>
        </div>        
    );
    

    function handleChange(e){
        const name = e.target.name;
        const value = e.target.value;
        setDepartment({...department,[name]:value})
    }
}