import React, {useState, useEffect} from 'react';
import { collection,addDoc,setDoc,getDocs } from 'firebase/firestore';

import {db} from '../dbmodule/firebase';
import './todo.css';

const todocollection = 'todos';

const Todo = () => {
    const [todo, setTodo] = useState('');
    const [todos,setTodos] = useState([]);

    const addToDo = async (e) => {
        e.preventDefault();

        try{
            const docRef = await addDoc(collection(db,todocollection),{todo:todo});
            console.log('Document written with ID: ' + docRef.id);
        }catch(e){
            console.error('Error adding document: ', e);
        }
    }

    const fetchPost = async () => {
        await getDocs(collection(db,todocollection)).then(
            ((querySnapshot)=>{
                const newData = querySnapshot.docs.map((doc) => ({...doc.data(),id:doc.id}));
                setTodos(newData);
                console.log(todos,newData);
            })
        )
    }
    useEffect(()=>{
        fetchPost();
    },[])
    
    return(
        <section className='todo-container'>
            <div className='todo'>
                <h1 className='header'>Todo-App</h1>
            </div>
            <div>
                <div>
                    <input
                        className='myinput' 
                        type='text' 
                        placeholder='What do you have todo today?'
                        onChange={(e)=>setTodo(e.target.value)}
                    />                                               
                </div>
                <div className='btn-container'>
                    <button
                        type='submit'
                        className='btn'
                        onClick={(e) => {
                            addToDo(e);
                            setTodos([...todos,{todo:todo,id:1}])
                        }}
                    >Submit</button>
                </div>
            </div>
            <div className='todo-content'>
                {todos?.map((todo,i)=>(
                    <p key={i}>{todo.todo}</p>
                ))}
            </div>
        </section>
    );
}

export default Todo;