import { useState } from "react";
import { UserData } from "./data/dummydata";

export default function LoginApp({onLogin}){
    const [username, setUserName] = useState('');
    const [password, setPassword] = useState('');
    //const [loginSuccess, setLoginSuccess] = useState(false);
    const [showError, setShowError] = useState(false);

    const defaultusername = 'itd';
    const defaultpassword = '<itd12345678>';

    function handleChange(e){
        setShowError(false);
        if(e.target.name === 'username')
            setUserName(e.target.value);
        else if(e.target.name==='password')
            setPassword(e.target.value);
    }

    return(<>
        <div className="divcontainer"
            style={{
                    height:'200px',
                    width:'300px',
                    border:'2px solid blue',
                    borderRadius:'10px',
                    padding:'20px',
                    margin:'auto',
                    display: "block",   
                    justifyContent:"center",
                    alignItems:'center'
                    }}>
            <input type='text' 
                name = 'username'
                placeholder="User name" 
                value={username}
                onChange={(e) => handleChange(e)} 
            />
            <input type='password' 
                name='password'
                placeholder='Password'
                value={password}
                onChange={(e) => handleChange(e)}
            /> 
            <button style={{                
                left:'47%'
            }}
                onClick={() =>{
                //let ls = (username === defaultusername && password === defaultpassword ? true : false)
                const ls = AuthenticateUser(username,password);
                console.log("Hello LS : " + ls);
                if(ls===0) setShowError(true);
                //console.log('au :' + ls)
                else onLogin(ls)
            }}>Login</button>
            {showError ? (<p style={{color:"red"}}>Wrong</p>) : ''}
        </div>
    </>);    
}

function AuthenticateUser(username, pwd){
    const authen = UserData.filter((u)=> (u.username === username && u.pwd === pwd))
    //console.log(authen.length);
    if(authen.length !==0) {
        //console.log("group: " + authen[0].usergroup);
        return authen[0].usergroup;
    }
    else return 0;
}