import { useState } from "react";
import { usePosition,usePositionDispatch } from "./positioncontext";

export default function ListPosition(){
    const positions = usePosition();    
    return(        
        <ul>
            {positions.map((p) =>{
                    return(
                    <li style={{'list-style-type':'none'}} key={p.positionid}><Position position={p}/></li>
                )
            })}
        </ul>
    )
}

function Position({position}){
    const [isEditing, setIsEditing] = useState(false);
    const positionDispatch = usePositionDispatch();
    let positioncontent;

    if(isEditing){
        positioncontent = (<>
            <input name='positionname' value={position.positionname} onChange={(e) =>{
                positionDispatch({
                    type:'changed',
                    position:{...position,positionname:e.target.value}
                })
            }} />
            <input name='positionfullname' value={position.positionfullname} onChange={(e) =>{
                positionDispatch({
                    type:'changed',
                    position: {...position, positionfullname:e.target.value},
                });
            }} />
            <button onClick={()=>setIsEditing(false)}>save</button>
        </>)
    }else{
        positioncontent = (<>
            {position.positionid} {position.positionname} {position.positionfullname}
            <button onClick={()=>setIsEditing(true)}>
                edit           
            </button>
        </>);
    }
    return(<form>
        {positioncontent}
        <button onClick={() => positionDispatch({
            type:'deleted',
            positionid:position.positionid,
        })}>delete</button>
    </form>)
}