import { useRef } from "react";
import './catfriends.css';

export default function CatFriednsApp(){
    const firstCatRef = useRef(null);
    const secondCatRef = useRef(null);
    const thirdCatRef = useRef(null);

    function handleScrollToFirstCat(){
        firstCatRef.current.scrollIntoView({
            behavior: 'smooth',
            block: 'nearest',
            inline: 'center'
        });
    }
    function handleScrollSecondCat(){
        secondCatRef.current.scrollIntoView({
            behavior: 'smooth',
            block: 'nearest',
            inline: 'center',
        });
    }
    function handleScrollThirdCat(){
        thirdCatRef.current.scrollIntoView({
            behavior: 'smooth',
            block: 'nearest',
            inline: 'center',
        });
    }

    return (<>
        <nav className="current">
            <button className="current" onClick={handleScrollToFirstCat}>Tom</button>
            <button className="current" onClick={handleScrollSecondCat}>Maru</button>
            <button className="current" onClick={handleScrollThirdCat}>Jellylorum</button>
        </nav>
        <div className="current">
            <ul className="current">
                <li className="current">
                    <img src="https://placekitten.com/g/200/200"
                    alt="Tom" ref={firstCatRef}/>
                </li>
                <li className="current">
                    <img src="https://placekitten.com/g/300/200"
                    alt="Maru"
                    ref={secondCatRef} />
                </li>
                <li className="current">
                    <img src="https://placekitten.com/g/250/200"
                    alt="Jellylorum"
                    ref={thirdCatRef} />
                </li>
            </ul>
        </div>
    </>)
}