import { useState } from 'react';
import './navbaranddropdownmenu.css'
import PositionApp from '../positionmodule/positionapp';
import DepartmentMain from '../departmentmodule/departmentmain';
import WorkOrderApp from '../requesterinput/workorderapp';
import UsingContextApp from '../usingcontext/usingcontextapp';
import DOMRefFormApp from '../manipulationthedomwithrefs/firstexample/inputfocuapp';
import CatFriednsApp from '../manipulationthedomwithrefs/example2scroll/cafriends';
import ITServicesSOP from '../sopmodule/itservicessop';
import { MenuData } from './menuData';
import { UserGroupPermission } from '../data/dummydata';
import LoginApp from '../login';
import Todo from '../todosmodule/todo';
import TaskApp from '../exampleaddtaskmodule/taskApp';
import ITForms from '../sopmodule/itforms';

export default function NavBar({usergroup}){

    const [selectedMenu, setSelectedMenu] = useState('');
    const menuData = MenuData;
    const userGroupPermission = UserGroupPermission;
    let content;

    function SelectedContent(){
        //alert(selectedMenu);
        {
            if(selectedMenu === 'position') return <PositionApp />
            else if(selectedMenu === 'department') return <DepartmentMain />
            else if(selectedMenu === 'itworkorder') return <WorkOrderApp />
            else if(selectedMenu === 'itservices') return <ITServicesSOP />
            else if(selectedMenu === 'domwithrefexample1') return <DOMRefFormApp />
            else if(selectedMenu === 'scrollcatpic1') return <CatFriednsApp />
            else if(selectedMenu === 'usingcontextapp') return <UsingContextApp />
            else if(selectedMenu === 'signout') return <LoginApp />
            else if(selectedMenu === 'exampletask') return <TaskApp />
            else if(selectedMenu === 'todo') return <Todo />
            else if(selectedMenu === 'itforms') return <ITForms />
        }
    }

    function handleOnClick(f){
        setSelectedMenu(f);
    }

    const permission = () => {
        //console.log('start');
        const g = userGroupPermission.filter((g) => g.usergroup === usergroup);
        if(g.length!==0)return g[0].permission;
    }

    return (<>
        <div className='navbar'>
            {permission().map((p) =>{
                const foundmenu = menuData.filter((m) => m.menuid === p)
                if(foundmenu.length !== 0){
                    return(<MainMenu menu={foundmenu[0]} onClick = {handleOnClick}/>)
                }
            })}
        </div>
        {/*
        <div className='navbar'>
           {menuData.map((menu) => {
                return <MainMenu menu={menu} onClick = {handleOnClick}/>
           })}
        </div>
        */}
        <div style={{border: '1px black solid' , width:'100%',height:'80vh'}}>{SelectedContent()}</div>
    </>)

    /*return(<>
        <div className="navbar">
            <div className='dropdown'><button className='dropbtn'>Home</button></div>
            <div className='dropdown'><button className='dropbtn'>Reports</button></div>*/
            {/*<a href="#news">Daily report</a>*/}
            /*<div className='dropdown'>
                <button className="dropbtn">Work order
                    <i className='fa fa-caret-down'></i>
                </button>
                <div className='dropdown-content'>
                    <a href="#" onClick={()=>setSelectedMenu('itworkorder')}>Add work order</a>
                    <a href="#">View work order</a>
                </div>
            </div>
            <div className='dropdown'>
                <button className='dropbtn'>SOP</button>
                <div className='dropdown-content'>
                    <a href='#' onClick={() =>setSelectedMenu('itservices')}>IT Services</a>
                </div>
            </div>
            <div className='dropdown'>
                <button className='dropbtn'>Others</button>
                <div className='dropdown-content'>
                    <a href="#" onClick={() => setSelectedMenu('department')}>Department</a>
                    <a href='#' onClick={() => setSelectedMenu('position')}>Position</a>
                </div>
            </div>
            <div className='dropdown'>
                <button className='dropbtn'>Example</button>
                <div className='dropdown-content'>
                    <a href='#' onClick={()=>setSelectedMenu('domwithrefexample1')}>Dom with useRef example 01</a>
                    <a href="#" onClick={()=>setSelectedMenu('scrollcatpic1')}>Scroll Example 1</a>
                    <a href='#' onClick={()=>setSelectedMenu('usingcontextapp')}>Using Context</a>
                </div>
            </div>
        </div>

        <div style={{border: '1px black solid' , width:'100%',height:'80vh'}}>{SelectedContent()}</div>
    </>)*/
}

function MainMenu({menu, onClick}){
    //return menu.submenu.map((submenu) =>(<div className='dropdown'><button className='dropbtn'>{menu.text}</button></div>));
    //console.log(menu.submenu);
    return (<div className='dropdown'>
                <button className='dropbtn'>{menu.text}</button>
                <div className='dropdown-content'>
                    {menu.function==='submenu' ? (menu.submenu.map((submenu)=> (<SubMenu submenu={submenu} onClick={onClick} />))) : ''}
                </div>
            </div>);
}

function SubMenu({submenu, onClick}){
    if(submenu.length!==0)
        return (<a href="#" onClick={()=>{onClick(submenu.function)}}>{submenu.text}</a>);
}